import {CHANGE_TOKEN, CHANGE_AVATAR, CHANGE_PHONE} from '../types';

const state = {
  privateToken: '',
  avatar: '',
  phone: ''
}

const getters = {
  getPrivateToken: state => state.privateToken,
  getAvatar: state => state.avatar,
  getPhone: state => state.phone
}

const mutations = {
  [CHANGE_TOKEN](state, token) {
    state.privateToken = token
  },
  [CHANGE_AVATAR](state, avatar) {
    state.avatar = avatar
  },
  [CHANGE_PHONE](state, phone) {
    state.phone = phone
  }
}

export default {
  getters,
  state,
  mutations
}



