import {GetCities, FetchBeCared, ChangeCare, GetCurrentCare, GetCarousels, GetJXServicesList, ReadMessage, GetZoneStore, GetOrderDetail, GetBecaredDetail, GetAssessments, AddBeCared, GetOrders, GetJrServicesList, GetHospitals, GetDepartments, AddAppointment, GetMyServicesList, AddMyAppoinment, AddJXAppoinment, UploadAvatar, Login, GetAuthCode, ValidateAuthCode, ResetPasswd, Register, ChangePasswd, GetCareAssessTpls, DeleteBeCare, ModifyBecared, GetMessageCenter, GetMomSercices, AddYSAppoinment, GetExtraOrderDetail, AssessmentsCare, GetMoonMomAssessTpls, GetIllnessCareAssessTpls, GetInfantMomCareAssessTpls, UpdateOrder, GetDetailMessage} from './resources'
 
export default {
  getCities: function (token) {
    return GetCities(token).get()
  },
  fetchBeCared: function () {
    return FetchBeCared.get()
  },
  addBeCared: function (data) {
    return AddBeCared.save(data)
  },
  getOrders: function (filter) {
  	return GetOrders.get({filter: filter})
  },
  getJrTypeServicesList: function (params) {
    return GetJrServicesList.get(params)
  },
  getOrderDetail: function (code) {
    return GetOrderDetail.get({code: code})
  },
  getBecaredDetail: function (id) {
    return GetBecaredDetail.get({id: id})
  },
  getHospitals: function (city) {
    return GetHospitals.get({city: city})
  },
  getDepartments: function(id) {
    return GetDepartments.get({id: id})
  },
  addAppointment: function(data) {
    return AddAppointment.save(data)
  },
  getMyServicesList: function(city) {
    return GetMyServicesList.get({city: city})
  },
  addMyAppointment: function(data) {
    return AddMyAppoinment.save(data);
  },
  getJXServicesList: function(city) {
    return GetJXServicesList.get({city: city})
  },
  addJXAppoinment: function(data) {
    return AddJXAppoinment.save(data);
  },
  uploadAvator: function(data) {
    return UploadAvatar.update(data);
  },
  login: function(data) {
    return Login().save(data);
  },
  getAuthCode: function(data) {
    return GetAuthCode.save(data);
  },
  validateAuthCode: function(data) {
    return ValidateAuthCode.save(data);
  },
  resetPasswd: function(data) {
    return ResetPasswd.update(data);
  },
  register: function(data) {
    return Register.save(data);
  },
  changePasswd: function(data) {
    return ChangePasswd.update(data);
  },
  deleteBeCare: function(id) {
    return DeleteBeCare.delete(id);
  },
  modifyBecared: function(data) {
    return ModifyBecared.update({id: data.id},{data: data});
  },
  getMessageCenter: function (data) {
    return GetMessageCenter.get(data);
  },
  getMomSercices: function() {
    return GetMomSercices.get();
  },
  getZoneStore: function(city) {
    return GetZoneStore.get({city: city})
  },
  addYSAppoinment: function(data) {
    return AddYSAppoinment.save(data);
  },
  getCurrentCare: function(code) {
    return GetCurrentCare.get({code: code});
  },
  changeCare: function(data) {
    return ChangeCare.save({code:data.code}, {data:data});
  },
  getextraOrderDetail: function(data) {
    console.log(data);
    return GetExtraOrderDetail.get({code: data.code,date: data.date})
  },
  //评价护工
  assessmentsCare: function(data) {
    return AssessmentsCare.save({code:data.code},{data: data});
  },
  //获得护工评价模版
  getCareAssessTpls: function(id) {
    return GetCareAssessTpls.get({id: id});
  },
  // 获得月嫂评价模版
  getMoonMomAssessTpls: function(id) {
    return GetMoonMomAssessTpls.get({id: id});
  },
  // 获得病患评估模板
  getIllnessCareAssessTpls: function(id) {
    return GetIllnessCareAssessTpls.get({id: id});
  },
  // 获得月嫂母婴评估模板
  getInfantMomCareAssessTpls: function(id) {
    return GetInfantMomCareAssessTpls.get({id: id});
  },
  //更新订单
  updateOrder: function(data) {
    return UpdateOrder.update({code: data.code},{action: data.action});
  },
  //获得订单评价列表
  getAssessments: function(code) {
    return GetAssessments.get({code: code});
  },
  getCarousels: function() {
    return GetCarousels().get();
  },
  getDetailMessage: function(id) {
    return GetDetailMessage.get({id: id});
  },
  readMessage: function(id) {
    return ReadMessage.update({id: id},{id: id});
  }
}


