import {GET_GLOBALVAR, CHANGE_BECARED_STATUS, CHANGE_INDEX_STATUS, CHANGE_HOME, CHANGE_COOMPANYTYPE, CHANGE_SERVICEINDEX, CHANGE_JRORJX, CHANGE_IF_INDEX, CHANGE_IF_MY_INTOB_ECARED_MANAGE, CHANGE_BECAREDID, CHANGE_SERVICE_TYPE,CHANGE_EVASLUATESCORE, CHANGE_CURRENT_CITY, CHANGE_INFANTMOM, CHANGE_SELFCARE_ABILITY, CHANGE_ILLNESS_LEVEL, CHANGE_INITSTATUS, CHANGE_TABSELECTED, CHANGE_ZONE_STORE} from '../types';

const state = {
  ifIndex: 1,
  ifMyIntoBeCaredManage: 9,
  serviceType: '',
  evaluateScore: '',
  beCaredId: null,
  currentCity: '',
  jrOrJx: '',
  companyType: '',
  selfCareAbility: [],
  illnessLevel: [],
  infantMom: {},
  serviceIndex: [],
  init: 'true',
  publicToken: 'Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJyb2xlIjoicHVibGljIiwiaWF0IjoxNDc1NDg0NzEzfQ.se7N-G2CTU2caWsgBCifv3yg08tf-7sGx4QwEbQe3yM',
  becaredStatus: true,
  tabSelected: '首页',
  store: [],
  indexStatus: '',
  home: false,
}

const getters = {
  getGlodalVal: state => state,
  currentCity: state => state.currentCity
}

const mutations = {
  [CHANGE_IF_INDEX](state, playload) {
    state.ifIndex = playload.ifIndex
  },
  [CHANGE_IF_MY_INTOB_ECARED_MANAGE](state, playload) {
    state.ifMyIntoBeCaredManage = playload.ifMyIntoBeCaredManage
  },
  [CHANGE_SERVICE_TYPE](state, playload) {
    state.serviceType = playload.serviceType
  },
  [CHANGE_EVASLUATESCORE](state, evaluateScore) {
    state.evaluateScore = evaluateScore
  },
  [CHANGE_BECAREDID](state, id) {
    state.beCaredId = id
  },
  [CHANGE_CURRENT_CITY](state, city) {
    state.currentCity = city
  },
  [CHANGE_JRORJX](state, type) {
    state.jrOrJx = type
  },
  [CHANGE_COOMPANYTYPE](state, type) {
    state.companyType = type;
  },
  [CHANGE_SELFCARE_ABILITY](state, selfCareAbility) {
    state.selfCareAbility = selfCareAbility;
  },
  [CHANGE_ILLNESS_LEVEL](state, illnessLevel) {
    state.illnessLevel = illnessLevel;
  },
  [CHANGE_INFANTMOM](state, infantMom) {
    state.infantMom = infantMom;
  },
  [CHANGE_SERVICEINDEX](state, serviceIndex) {
    state.serviceIndex = serviceIndex;
  },
  [CHANGE_INITSTATUS](state, init) {
    state.init = init;
  },
  [CHANGE_BECARED_STATUS](state, status) {
    state.becaredStatus = status
  },
  [CHANGE_TABSELECTED](state, tabSelected) {
    state.tabSelected = tabSelected;
  },
  [CHANGE_ZONE_STORE](state, store) {
    state.store = store;
  },
  [CHANGE_INDEX_STATUS](state,indexStatus) {
    state.indexStatus = indexStatus;
  },
  [CHANGE_HOME](state,home) {
    state.home = home;
  },
}

export default {
  state,
  getters,
  mutations
}


