import {CHANGE_HOSPITAILS} from '../types';

const state = {
  hospitals: []
}

const getters = {
  getHospitals: state => state.hospitals
}

const mutations = {
  [CHANGE_HOSPITAILS](state, hospitals) {
    state.hospitals = hospitals
  }
}

export default{
  state,
  getters,
  mutations
}


