<template>
  <div class="containerItem">
    <div class="indexbanner">
      <span class="text">预约服务</span>
    </div>
    <Swriper></Swriper>
    <IndexServicesBody></IndexServicesBody>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import Swriper from './Swriper';
import IndexServicesBody from './IndexServicesBody';
export default {
  name: 'index',
  components:{
    Swriper,
    IndexServicesBody
  },
  data() {
    return {
    };
  },
  computed: {
    ...mapGetters({
      privateToken: 'getPrivateToken'
    }),
  },
  methods: {
    ...mapActions({
      changeIndexStatus: 'changeIndexStatus'
    })
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.changeIndexStatus('index');
    })
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.chooseBeCared .mint-actionsheet-listitem{
    font-size: 0.5rem;
    line-height: 1.2rem;
    height: 1.2rem;
}
</style>



