<template>
  <div class="indexServicesBody">
    <IndexServicesItem class="indexServicesItem">
        <p slot="title">医院服务</p>
        <div slot="body">
          <a @click="toHospital">
            <div>
              <img src="../assets/index_hospital.png" alt="">
            </div>
            <div>
              病患陪护
            </div>
          </a>
          <router-link :to="{path: 'chooseBeCared', query: { clickType: 'index', serviceType: 'muyin' }}">
            <div>
              <img src="../assets/index_my.png" alt="">
            </div>
            <div>
              母婴陪护
            </div>
          </router-link>
          <router-link :to="{path: 'chooseBeCared', query: { clickType: 'index', serviceType: 'jixiang' }}">
            <div>
              <img src="../assets/index_jx.png" alt="">
            </div>
            <div>
              计项服务
            </div>
          </router-link>
        </div>
    </IndexServicesItem>
    <IndexServicesItem class="indexServicesItem">
        <p slot="title">家庭服务</p>
        <div slot="body">
          <a @click="toHomePatient">
            <div>
              <img src="../assets/index_family.png" alt="">
            </div>
            <div>
              病患陪护
            </div>
          </a>
          <router-link :to="{path: 'chooseBeCared', query: { clickType: 'index', serviceType: 'yuesao' }}">
            <div>
              <img src="../assets/index_ys.png" alt="">
            </div>
            <div>
              月嫂陪护
            </div>
          </router-link>
        </div>
    </IndexServicesItem>
  </div>
</template>

<script>
import IndexServicesItem from './IndexServicesItem';
import { mapGetters, mapActions} from 'vuex';
export default {
  name: 'indexServicesBody',
  data () {
    return {
    }
  },
  components:{
    IndexServicesItem
  },
  methods: {
    ...mapActions({
      changeHome: 'changeHome'
    }),
    toHomePatient: function() {
      this.changeHome(true);
      this.$router.push({path: 'chooseBeCared', query: {clickType: 'index', serviceType: 'patient' }})
    },
    toHospital: function() {
      this.changeHome(false);
      this.$router.push({path: 'chooseBeCared', query: { clickType: 'index', serviceType: 'patient'}})
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.indexServicesItem {
  margin-top: 5%;
  overflow: hidden;
}
.indexServicesItem p {
  text-indent: 3%;
}
.indexServicesItem a{
  width: 33.3%;
  float: left;
  text-decoration: none;
  color: black;
}
.indexServicesItem img{
  width: 60%;
}
.indexServicesItem a div{
  text-align: center;
}
.indexServicesBody {
  height: 54%;
  overflow: scroll;
  font-size: 0.4rem;
}
</style>



