import {CHANGE_YS_SERVICESLIST} from '../types';
import {serviceDurationUnitFilter} from '../../utils/filters';
const state = {
  services: []
}

const getters = {
  getYsServices: state => state.services
}

const mutations = {
  [CHANGE_YS_SERVICESLIST](state, services) {
    services.forEach(i => {
      i['servicesName'] = i.price + '元/' + i.duration + '' + serviceDurationUnitFilter(i.unit);
    })
    state.services = services
  }
}

export default{
  state,
  getters,
  mutations
}


