import {CHANGE_RECOMMENDS, CHANGE_OTHERS} from '../types';
const state = {
  recommends: [], // 推荐类服务
  others: []
}

const getters = {
  getRecommends: state => state.recommends,
  others: state => state.others
}

const mutations = {
  [CHANGE_RECOMMENDS](state, recommends) {
    state.recommends = recommends
  },
  [CHANGE_OTHERS](state, others) {
    state.others = others
  }
}

export default{
  state,
  getters,
  mutations
}


