const ChooseBeCared = r => require.ensure([], () => r(require('./views/ChooseBeCared.vue')), '/chooseBeCared');
const ChangeBeCared = r => require.ensure([], () => r(require('./views/ChangeBeCared.vue')), '/changeBeCared');
const OrderDetail = r => require.ensure([], () => r(require('./views/OrderDetail.vue')), '/orderDetail');
const PatientEvaluate = r => require.ensure([], () => r(require('./views/PatientEvaluate.vue')), '/patientEvaluate');
const MuYinEvaluate = r => require.ensure([], () => r(require('./views/MuYinEvaluate.vue')), '/muYinEvaluate');
const JxServicesList = r => require.ensure([], () => r(require('./views/JxServicesList.vue')), '/jxServicesList');
const JrServicesList = r => require.ensure([], () => r(require('./views/JrServicesList.vue')), '/jrServicesList');
const Appointment = r => require.ensure([], () => r(require('./views/Appointment.vue')), '/appointment');
const MyServicesList = r => require.ensure([], () => r(require('./views/MyServicesList.vue')), '/myServicesList');
const Login = r => require.ensure([], () => r(require('./views/Login.vue')), '/login');
const ForgetPasswd = r => require.ensure([], () => r(require('./views/ForgetPasswd.vue')), '/forgetPasswd');
const Register =  r => require.ensure([], () => r(require('./views/Register.vue')), '/register');
const Protocol =  r => require.ensure([], () => r(require('./views/Protocol.vue')), '/protocol');
const More =  r => require.ensure([], () => r(require('./views/More.vue')), '/more');
//const FeedBack =  r => require.ensure([], () => r(require('./views/FeedBack.vue')), '/feedBack');
const ChangePasswd =  r => require.ensure([], () => r(require('./views/ChangePasswd.vue')), '/changePasswd');
const MessageCenter = r => require.ensure([], () => r(require('./views/MessageCenter.vue')), '/messageCenter');
const YsServicesList = r => require.ensure([], () => r(require('./views/YsServicesList.vue')), '/ysServicesList');
const CurrentCareDetail = r => require.ensure([], () => r(require('./views/CurrentCareDetail.vue')), '/currentCareDetail');
const ExtraOrderDetail = r => require.ensure([], () => r(require('./views/ExtraOrderDetail.vue')), '/extraOrderDetail');
const IndexOrder = r => require.ensure([], () => r(require('./components/IndexOrder.vue')), '/order');
const IndexMy = r => require.ensure([], () => r(require('./components/IndexMy.vue')), '/mymenu');
const Evaluate = r => require.ensure([], () => r(require('./views/Evaluate.vue')), '/evaluate');

const routes = [
  { path: '/index', 
    component: require('./views/Index.vue'),
    children: [
      {
        path: '',
        component: require('./components/Index.vue')
      },
      {
        path: 'order',
        component: IndexOrder
      },
      {
        path: 'mymenu',
        component: IndexMy
      }
    ]
  },
  { path: '/', 
    component: require('./views/App.vue'),
    children: [
      { path: 'chooseBeCared', component: ChooseBeCared },
      { path: 'changeBeCared', component: ChangeBeCared },
      { path: 'orderDetail', component: OrderDetail },
      { path: 'patientEvaluate', component: PatientEvaluate },
      { path: 'muYinEvaluate', component: MuYinEvaluate },
      { path: 'jiXiangEvaluate', component: JxServicesList },
      { path: 'jrServicesList', component: JrServicesList },
      { path: 'appointment', component: Appointment },
      { path: 'myServicesList', component: MyServicesList },
      { path: '', component: Login },
      { path: 'forgetPasswd', component: ForgetPasswd },
      { path: 'register', component: Register },
      { path: 'protocol', component: Protocol },
      { path: 'more', component: More },
      //{ path: 'feedBack', component: FeedBack },
      { path: 'changePasswd', component: ChangePasswd },
      { path: 'messageCenter', component: MessageCenter },
      { path: 'ysServicesList', component: YsServicesList },
      { path: 'currentCareDetail', component: CurrentCareDetail },
      { path: 'extraOrderDetail', component: ExtraOrderDetail },
      { path: 'evaluate', component: Evaluate }  
    ]
  },
]

export default routes;


