import Vue from 'vue';
import App from './App';
import VueRouter from 'vue-router';
import VueValidator from 'vue-validator';
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css'
import routes from './routers';
//import { sexFilter, nationFilter, parturitionFilter, serviceDurationUnitFilter, serviceTypeFilter} from './utils/filters';
import {serviceTypeFilter, nationFilter, sexFilter} from './utils/filters';

Vue.use(VueRouter);
Vue.use(VueValidator);
Vue.use(Mint);
Vue.filter('serviceTypeFilter', value => {
  return serviceTypeFilter(value);
});
Vue.filter('nationFilter', value => {
  return nationFilter(value);
});
Vue.filter('sexFilter', value => {
  return sexFilter(value);
});
Vue.filter('timeFilter', value => {
  return value.split(' ')[1];
});
Vue.filter('birthdayFilter', value => {
  if(value) {
    return '未知';
  } else {
    return new Date().getFullYear() - new Date(value).getFullYear();
  }
});
Vue.filter('blankFilter', value => {
  if(value) {
    return value;
  } else {
    return '-';
  }
});
//Object.keys(filters.forEach(k => Vue.filter(k, filters[k])));

const router = new VueRouter({
  mode: 'history',
  saveScrollPosition: true,
  suppressTransitionError: true,
  routes
})

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})




