import Vuex from 'vuex'
import Vue from 'vue'
import actions from './actions'
import middlewares from './middlewares'
import provinceCity from './modules/provinceCity'
import globalVal from './modules/globalVal'
import beCaredList from './modules/beCaredList'
import orderList from './modules/orderList'
import jrTypeServicesList from './modules/jrTypeServicesList'
import orderDetail from './modules/orderDetail'
import beCaredDetail from './modules/beCaredDetail'
import hospitails from './modules/hospitails'
import departments from './modules/departments'
import myTypeServicesList from './modules/myTypeServicesList'
import jxTypeServicesList from './modules/jxTypeServicesList'
import userInfo from './modules/userInfo'
import messageCenter from './modules/messageCenter'
import ysTypeServicesList from './modules/ysTypeServicesList'

const debug = process.env.NODE_ENV !== 'production'
Vue.use(Vuex)

const store = new Vuex.Store({
  actions,
  modules: {
    provinceCity,
    globalVal,
    beCaredList,
    orderList,
    jrTypeServicesList,
    orderDetail,
    beCaredDetail,
    hospitails,
    departments,
    myTypeServicesList,
    jxTypeServicesList,
    userInfo,
    messageCenter,
    ysTypeServicesList
  },
  strict: false,
  plugins: middlewares
})

export default store


