import {CHANGE_ORDER_DETAIL} from '../types';
import {hgStatus, detailMoreInfo} from '../../utils/util'
const state = {
  orderDetail: {
    name: ''
  }
}

const getters = {
  orderDetail: state => state.orderDetail
}

const mutations = {
  [CHANGE_ORDER_DETAIL](state, orderDetail) {
    state.orderDetail = hgStatus(orderDetail, 'obj');
    state.orderDetail = detailMoreInfo(orderDetail);
  }
}

export default{
  state,
  getters,
  mutations
}


