import { GET_CITIES, CHANGE_CITIES } from '../types';
//import api from  '../../api'
const state = {
  cities: [],
  provinces: []
}

const getters = {
  getCities: state => state.cities
}

const mutations = {
  [CHANGE_CITIES](state, playload) {
    state.cities = playload.cities
  }
}

export default {
  getters,
  state,
  mutations
}


