import {CHANGE_MY_SERVICESLIST} from '../types';

const state = {
  services: [],
}

const getters = {
  getMyServices: state => state.services
}

const mutations = {
  [CHANGE_MY_SERVICESLIST](state, services) {
    state.services = services
  }
}

export default{
  state,
  getters,
  mutations
}


