import * as filters from './filters';
export const checkBlank = obj => {
  const keys = Object.keys(obj);
  for(let i = 0; i < keys.length; i++) {
    if(!obj[keys[i]]) {
      return false;
    }
  }
  return true;
}

export const check = (obj, arr, cb) => {
  const keys = Object.keys(obj);
  console.log(keys);
  for(let i = 0; i < keys.length; i++) {
    console.log(arr);
    if(!obj[keys[i]] && obj[keys[i]] != 0) {
      cb({
          message: arr[i],
          position: 'bottom',
          duration: 1000
        })
      return false;
    }
  }
  return true;
}

export const checkPhone = str => {
  const reg = /^1(3[0-9]|4[57]|5[0-35-9]|7[0135678]|8[0-9])\d{8}$/;
  return reg.test(str);
}

export const hgStatus = (param, option) => {
  if(option == 'obj') {
      param['hgStatus'] = '已预约';
      if(param.careStatus == 1 && param.managerStatus >3) {
        param['hgStatus'] = '未上工';
      }
      if(param.careStatus == 2 && param.managerStatus >3) {
        param['hgStatus'] = '上工中';
      }
      if(param.careStatus == 3 && param.managerStatus >3) {
        param['hgStatus'] = '已下工';
      }
      if(param.managerStatus == 5) {
        param['hgStatus'] = '已取消';
      }
      if(param.managerStatus == 6) {
        param['hgStatus'] = '已完成';
      }

      param['navtitle'] = '';
      if(param.careStatus == 1 && param.managerStatus < 5) {
        param['navtitle'] = '取消订单';
      }
      if(param.careStatus == 2) {
        param['navtitle'] = '提前结单';
      }
    return param;
  } else {
    param.forEach((i, index)=> {
      param[index]['hgStatus'] = '已预约';
      if(i.careStatus == 1 && i.managerStatus >3) {
          param[index]['hgStatus'] = '未上工';
        } 
      if(i.careStatus == 2 && i.managerStatus >3) {
          param[index]['hgStatus'] = '上工中';
        }
      if(i.careStatus == 3 && i.managerStatus >3) {
          param[index]['hgStatus'] = '已下工';
        }
      if(i.managerStatus == 5) {
          param[index]['hgStatus'] = '已取消';
        }
      if(i.managerStatus == 6) {
          param[index]['hgStatus'] = '已完成';
        }
      })
    return param;
  }
}

export const detailMoreInfo = function(obj) {
  if(obj.serviceType == 1 ) {
    obj['careDetail'] = false;
  } else {
    if(obj.careStatus > 1 && obj.managerStatus != 5) {
      obj['careDetail'] = true;
    } else {
      obj['careDetail'] = false;
    }
  }

  if(obj.managerStatus == 4) {
    obj['changeCare'] = true;
  } else {
    obj['changeCare'] = false;
  }

  if(obj.serviceType == 1) {
    obj['renew'] = false;
  } else {
    if(obj.careStatus == 2) {
      obj['renew'] = true;
    } else {
      obj['renew'] = false;
    }
  }

  //评价按钮 不确定出现的时机
  if(obj.managerStatus == 6) {
    obj['evaluate'] = true;
  } else {
    obj['evaluate'] = false;
  }

  return obj;
}

export const careChange = function(obj) {
  /*vm.currentCare.age = new Date().getFullYear() - response.data.birthday.slice(0,4);*/
  obj['sex'] = filters.sexFilter(obj['sex']);
  obj['nation'] = filters.nationFilter(obj['nation']);
  obj['age'] = new Date().getFullYear() - obj['birthday'].slice(0,4);
  obj['level'] = filters.levelFilter(obj['level']);

  return obj;
}

export const checkError = function(obj) {
  if(obj.data.code) {
    switch(obj.data.code) {
      case -1:
        return '未知错误';
      case 404:
        return 'Not Found';
      case 500:
        return '服务器内部错误';
      case 1001:
        return '参数非法';
      case 2001:
        return '用户已存在';
      case 2002:
        return '未注册或用户名密码错误';
      case 2003:
        return '注册角色受限';
      case 3001:
        return 'API权限不足';
      case 3002:
        return '禁止访问';
      case 4001:
        return '资源不存在';
      case 4002:
        return '资源重复';
      case 5001:
        return '短信服务错误';
      case 5002:
        return '验证码无效';
      case 5003:
        return '请求验证码过快';
      case 6001:
        return '文件不存在';
      case 6002:
        return '文件格式不合法';
      case 6003:
        return '文件大小不合法';
      default:
        return '';
    }
  }
}


