<template>
  <div class="index bottomnav">
    <router-view></router-view>
     <div class="w_tabbar bottomBanner w_is_fixed">
      <a class="w_tab_item" @click="toIndex" :class="indexclass" ref="index">
        <div class="w_tab_item_icon">
          <img slot="icon" src="../assets/index.png">
          <div class="w_tab_item_label">
            首页 {{text}}
          </div>
        </div>
      </a>
      <a class="w_tab_item " @click="toOrder" :class="orderclass" ref="order">
        <div class="w_tab_item_icon">
          <img slot="icon" src="../assets/order.png">
          <div class="w_tab_item_label">
            订单
          </div>
        </div>
      </a>
      <a class="w_tab_item" @click="toMy" :class="myclass" ref="my">
        <div class="w_tab_item_icon">
          <img slot="icon" src="../assets/my.png">
          <div class="w_tab_item_label">
            我的
          </div>
        </div>
      </a>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'app',
  data() {
    return {
      indexclass: 'w_tab_item',
      orderclass: 'w_tab_item',
      myclass: 'w_tab_item'
    }
  },
  computed:{
    ...mapGetters({
      globalVal:'getGlodalVal'
    }),
    text: function() {
      if(this.globalVal.indexStatus == 'order') {
        this.indexclass = 'w_tab_item';
        this.orderclass = 'w_tab_item is_selected';
        this.myclass = 'w_tab_item';
      } else if(this.globalVal.indexStatus == 'my') {
        this.indexclass = 'w_tab_item';
        this.orderclass = 'w_tab_item';
        this.myclass = 'w_tab_item is_selected';
      } else {
        this.indexclass = 'w_tab_item is_selected';
        this.orderclass = 'w_tab_item';
        this.myclass = 'w_tab_item';
      }
      return '';
    }
  },
  methods: {
    ...mapActions({
      changeIndexStatus: 'changeIndexStatus'
    }),
    toIndex: function() {
      this.changeIndexStatus('index');
      this.$router.push('/index')
    },
    toOrder: function() {
      this.changeIndexStatus('order');
      this.$router.push('/index/order');
    },
    toMy: function() {
      this.changeIndexStatus('my');
      this.$router.push('/index/mymenu')
    }
  },
  watch: {
    globalVal: function() {

    }
  },
  created() {
    
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
* {
  margin: 0px;
  padding: 0px;
}
.w_tabbar.w_is_fixed{
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
  }
  .w_tabbar {
        position: relative;
    background-color: #fafafa;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    right: 0;
    bottom: 0;
    left: 0;
    position: absolute;
    text-align: center;
  }
  .w_tabbar > .w_tab_item.is_selected {
    background-color: #eaeaea;
    color: #26a2ff;
}
.bottomnav .w_tab_item {
    padding: 0.1rem 0;
}
  .w_tab_item{
    display: block;
    padding: 7px 0;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
  }
  .w_tab_item_icon{
       width: 0.6rem;
    height: 0.6rem;
    margin: 0 auto 5px;
  }
  .w_tab_item_icon > * {
    display: block;
    width: 100%;
    height: 100%;
}
.w_tab_item_label {
  color: inherit;
  font-size: 0.3rem;
  line-height: 1;
  min-width: 0.7rem;
}
  .indexbanner {
    font-size: 0.2rem;
    display: flex;
    margin-top: 2%;
    height: 3%;
    margin-bottom: 2%;
    justify-content: space-between;
  }
  .text {
    color: #525656;
    margin-left: 0.12rem;
    font-size: 0.4rem;
    margin-top: 2px;
  }
  .bottomnav .mint-button {
    background: url(../assets/downshow.png) no-repeat;
    height: 100%;
    line-height: 100%;
    width: 100%;
    right: -18%;
    font-size: 0.4rem;
    background-position: 74.3% 30%;
    box-shadow: none;
  }
  .bottomnav .mint-button::after {
     background-color: #fff;
  }
  .page-tabbar {
    overflow: hidden;
    height: 100vh;
  }
  .page-wrap {
    overflow: auto;
    height: 92%;
  }
  .container {
    height: 100%;
  }
  .containerItem {
    height: 100%;
  }
  .mint-tab-container-wrap {
    height: 100%;
  }
  .bottomBanner {
    height: 8%;
  }
  .page-part {
    height: 100%;
  }
  .bottomnav .mint-actionsheet-listitem {
    font-size: 0.5rem;
    line-height: 1.2rem;
    height: 1.2rem;
  }
  .bottomnav .mint-tab-item {
    padding: 0.1rem 0;
  }
  .bottomnav .mint-tab-item-icon {
    width: 0.6rem;
    height: 0.6rem;
  }
  .mint-tab-item-label {
    font-size: 0.4rem;
  }
  .bottomnav .mint-tab-item-label {
    font-size: 0.3rem;
  }
  .bottomnav .Order .page-part .mint-tab-item-label {
    font-size: 0.4rem !important; 
  }
  .my {
    height: 92%;
  }
  .btn.mint-button .mint-button-text {
    font-size: 0.4rem;
  }
  .mint-tab-container-item {
    height: 100%;
  }
</style>



