import {CHANGE_DEPARTMENTS} from '../types';

const state = {
  departments: []
}

const getters = {
  getDepartments: state => state.departments
}

const mutations = {
  [CHANGE_DEPARTMENTS](state, departments) {
    state.departments = departments
  }
}

export default{
  state,
  getters,
  mutations
}


