import {CHANGE_BECARED_LSIT, SUCCESS_ADD_BECARED} from '../types';
import api from '../../api'

const state = {
  beCaredList: []
}

const getters = {
  getBeCaredList: state => state
}

const mutations = {
  [CHANGE_BECARED_LSIT](state, playload) {
    state.beCaredList = playload.beCaredList
  },
  [SUCCESS_ADD_BECARED](state, playload) {
    state.beCaredList.push(playload.beCaredListItem)
  }
}

export default {
  getters,
  state,
  mutations
}



