import nationsMap from './nationsMap.js';
import sexMap from './sexMap';
import parturitionMap from './parturitionMap';
import serviceTypeMap from './serviceTypeMap';
import serviceDurationUnitMap from './serviceDurationUnitMap';
import patientEvaluateMap from './patientEvaluateMap';
import levelMap from './levelMap';

const BiMap = require('bimap');

let nation = new BiMap;
let sex = new BiMap;
let parturition = new BiMap;
let serviceType = new BiMap;
let serviceDurationUnit = new BiMap;
let patientEvaluate = new BiMap;
let level = new BiMap;

bimapFactory(nationsMap, nation);
bimapFactory(sexMap, sex);
bimapFactory(parturitionMap, parturition);
bimapFactory(serviceTypeMap, serviceType);
bimapFactory(serviceDurationUnitMap, serviceDurationUnit);
bimapFactory(patientEvaluateMap, patientEvaluate);
bimapFactory(levelMap, level);

function bimapFactory(map, bimap) {
  Object.keys(map).forEach(function(item) {
    bimap.push(item, map[item]);
  });
}

export {
  nation,
  sex,
  parturition,
  serviceType,
  serviceDurationUnit,
  patientEvaluate,
  level
}


