<template>
  <div class="swiper">
    <mt-swipe  :auto="4000" :show-indicators="false">
      <mt-swipe-item v-for="pt in pts">
        <a :href="pt.url">
          <img :src="pt.image">
        </a>
      </mt-swipe-item>
    </mt-swipe>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'swiper',
  data () {
    return {
      pts:[]
    }
  },
  methods: {
    ...mapActions({
      getCarousels: 'getCarousels'
    })
  },
  created() {
    this.getCarousels().then(response => {
      if(response.ok) {
        this.pts = response.data;
      }
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.swiper {
  width: 100%;
  height: 35%;
}
img {
  width: 100%;
  height: 100%;
}
</style>



