import { nation, sex, parturition, serviceDurationUnit, serviceType, level } from './maps';
export const sexFilter = sexNum => {
  return sex.val(sexNum);
}

export const nationFilter = nationNum => {
  return nation.val(nationNum);
}

export const parturitionFilter = parturitionNum => {
  return parturition.val(parturitionNum);
}

export const serviceDurationUnitFilter = serviceDurationUnitNum => {
  return serviceDurationUnit.val(serviceDurationUnitNum);
}

export const serviceTypeFilter = serviceTypeNum => {
  return serviceType.val(serviceTypeNum);
}

export const levelFilter = levelNum => {
  return level.val(levelNum);
}

export const timeFilter = time => {
  const newTime = new Date(time);
  const year = newTime.getFullYear();
  const month = addZero(newTime.getMonth() + 1);
  const day = addZero(newTime.getDate());
  const hour = addZero(newTime.getHours());
  const minute = addZero(newTime.getMinutes());
  const second = addZero(newTime.getSeconds());
  const formatDate = `${year}-${month}-${day} ${hour}:${minute}:${second}`;

  return formatDate;
}
export const addzero = num => {
  if(num < 10) {
    return '0' + ''+ num;
  } else {
    return num;
  }
}

function addZero (num) {
  if(num < 10) {
    return '0' + ''+ num;
  } else {
    return num;
  }
}


