import {GET_MESSAGE_CENTER} from '../types';
const state = {
  messageCenter: {}
}

const getters = {
  getMessageCenter: state => state.messageCenter
}

const mutations = {
  [GET_MESSAGE_CENTER](state, messageCenter) {
    state.messageCenter = messageCenter
  }
}

export default{
  state,
  getters,
  mutations
}


