import {CHANGE_NAVBAR, CHANGE_CONTAINER} from '../types';
const state = {
  navbars: [], // 推荐类服务
  containers: []
}

const getters = {
  getNavbars: state => state.navbars,
  getContainers: state => state.containers
}

const mutations = {
  [CHANGE_NAVBAR](state, navbars) {
    state.navbars = navbars
  },
  [CHANGE_CONTAINER](state, containers) {
    state.containers = containers
  }
}

export default{
  state,
  getters,
  mutations
}


