import api from  '../api'
import * as types from './types'

export default {
  // 获取城市信息
  fetchCities ({ commit }, token) {
    return api.getCities(token).then(response => {
      commit(types.CHANGE_CITIES, {cities: response.data})
      return response;
    })
  },
  // 获取被护理人信息
  fetchBeCared ({ commit }) {
    return api.fetchBeCared().then(response => {
      commit(types.CHANGE_BECARED_LSIT, {beCaredList: response.data.items})
    })
  },
  // 增加被护理人
  addBeCared ({ commit }, data) {
    return api.addBeCared(data).then(response => {
      commit(types.SUCCESS_ADD_BECARED, {beCaredListItem: response.data});
      return response;
    })
  },
  // 获取订单
  getOrders ({ commit }, filter) {
    return api.getOrders(filter).then(response => {
      if(filter == 'all') {
        commit(types.CHANGE_ORDERS, {orders: response.data})
      }
      if(filter == 'undone') {
        commit(types.CHANGE_UNDONEORDERS, {undoneOrders: response.data})
      }
      if(filter == 'done') {
        commit(types.CHANGE_DONEORDERS, {doneOrders: response.data})
      }
    })
  },
  // 改变全局分数
  changeEvaluateScore({ commit }, data) {
    commit(types.CHANGE_EVASLUATESCORE, data)
  },
  changeBecaredId({ commit }, id) {
     commit(types.CHANGE_BECAREDID, id)
  },
  fetchJrTypeServicesList({ commit }, params) {
    return api.getJrTypeServicesList(params).then(response => {
      console.log(response.data)
      commit(types.CHANGE_RECOMMENDS, response.data.recommends)
      commit(types.CHANGE_OTHERS, response.data.others)
    })
  },
  changeCurrentCity({commit}, currentCity) {
    commit(types.CHANGE_CURRENT_CITY, currentCity);
    return api.getHospitals(currentCity).then(response => {
      commit(types.CHANGE_HOSPITAILS, response.data)
    })
  },
  getOrderDetail({commit}, code) {
    return api.getOrderDetail(code).then(response => {
      commit(types.CHANGE_ORDER_DETAIL, response.data);
      return response;
    })
  },
  getBecaredDetail({commit}, id) {
    return api.getBecaredDetail(id).then(response => {
      commit(types.CHANGE_BECARED_DETAIL, response.data);
      return response;
    })
  },
  changejrOrJx({commit}, type) {
    commit(types.CHANGE_JRORJX, type)
  },
  changeCompanyType({commit}, type) {
    commit(types.CHANGE_COOMPANYTYPE, type)
  },
  fetchHospitals({commit}, city) {
    return api.getHospitals(city).then(response => {
      commit(types.CHANGE_HOSPITAILS, response.data)
    })
  },
  fetchDepartments({commit}, id) {
    return api.getDepartments(id).then(response => {
      commit(types.CHANGE_DEPARTMENTS, response.data);
    })
  },
  addAppointment({commit}, data) {
    return api.addAppointment(data);
  },
  changeSelfCareAbility({commit}, selfCareAbility) {
    commit(types.CHANGE_SELFCARE_ABILITY, selfCareAbility);
  },
  changeIllnessLevel({commit}, illnessLevel) {
    commit(types.CHANGE_ILLNESS_LEVEL, illnessLevel);
  },
  fetchMyServicesList({commit}, city) {
    return api.getMyServicesList(city).then(response => {
      commit(types.CHANGE_MY_SERVICESLIST, response.data)
    })
  },
  changeInfantMom({commit}, infantMom) {
    commit(types.CHANGE_INFANTMOM, infantMom)
  },
  addMyAppointment({commit}, data) {
    return api.addMyAppointment(data);
  },
  fetchJxServicesList({commit}, city) {
    return api.getJXServicesList(city).then(response => {
      let navbars = [];
      let containers = [];
      response.data[0].services.forEach(i => {
        navbars.push(i.title);
        let obj = {};
        obj.id = response.data[0].id;
        obj.title = i.title;
        obj.catalog = i.catalog;
        containers.push(obj);
      })
      commit(types.CHANGE_NAVBAR, navbars);
      commit(types.CHANGE_CONTAINER, containers);
      return response;
    })
  },
  changeServiceIndex({commit}, serviceIndex) {
    commit(types.CHANGE_SERVICEINDEX, serviceIndex);
  },
  addJXAppoinment({commit}, data) {
    return api.addJXAppoinment(data);
  },
  changeInit({commit}, state) {
    commit(types.CHANGE_INITSTATUS, state);
  },
  uploadAvatar({commit}, data) {
    return api.uploadAvator(data);
  },
  changeToken({commit}, token) {
    commit(types.CHANGE_TOKEN, token);
  },
  changeAvatar({commit}, avatar) {
    commit(types.CHANGE_AVATAR, avatar);
  },
  changePhone({commit}, phone) {
    commit(types.CHANGE_PHONE, phone);
  },
  login({commit}, data) {
    return api.login(data);
  },
  getAuthCode({commit}, data) {
    return api.getAuthCode(data);
  },
  validateAuthCode({commit}, data) {
    return api.validateAuthCode(data);
  },
  resetPasswd({commit}, data) {
    return api.resetPasswd(data);
  },
  register({commit}, data) {
    return api.register(data);
  },
  changePasswd({commit}, data) {
    return api.changePasswd(data);
  },
  deleteBeCare({commit}, id) {
    return api.deleteBeCare({id: id});
  },
  becaredStatus({commit}, status) {
    commit(types.CHANGE_BECARED_STATUS, status)
  },
  changeTabSelected({commit}, tabSelected) {
    commit(types.CHANGE_TABSELECTED, tabSelected)
  },
  modifyBecared ({ commit }, data) {
    return api.modifyBecared(data).then(response => {
      return response;
    })
  },
  getMessageCenter({commit}) {
    return api.getMessageCenter({isSender: false}).then(response => {
      commit(types.GET_MESSAGE_CENTER, response.data)
    });
  },
  getMomSercices({commit}) {
    return api.getMomSercices().then(reponse => {
      commit(types.CHANGE_YS_SERVICESLIST, reponse.data)
    })
  },
  getZoneStore({commit}, city) {
    return api.getZoneStore(city).then(response => {
      commit(types.CHANGE_ZONE_STORE, response.data)
    })
  },
  addYSAppoinment({commit}, data) {
    return api.addYSAppoinment(data);
  },
  getCurrentCare({commit}, code) {
    return api.getCurrentCare(code);
  },
  changeCare({commit}, data) {
    return api.changeCare(data);
  },
  getExtraOrderDetail({commit}, data) {
    return api.getextraOrderDetail(data);
  },
  // 评价护工
  assessmentsCare({commit}, data) {
    return api.assessmentsCare(data)
  },
  //获得护工评价模版
  getCareAssessTpls({commit}, id) {
    return api.getCareAssessTpls(id);
  },
  //获得月嫂评价模版
  getMoonMomAssessTpls({commit}, id) {
    return api.getMoonMomAssessTpls(id);
  },
  //获得病患评估模板
  getIllnessCareAssessTpls({commit}, id) {
    return api.getIllnessCareAssessTpls(id);
  },
  //获得月嫂母婴评估模板 
  getInfantMomCareAssessTpls({commit}, id) {
    return api.getInfantMomCareAssessTpls(id);
  },
  // 更新订单
  updateOrder({commit}, data) {
    return api.updateOrder(data);
  },
  changeIndexStatus({commit}, status) {
    commit(types.CHANGE_INDEX_STATUS, status);
  },
  getAssessments({commit}, code) {
    return api.getAssessments(code);
  },
  getCarousels({commit}) {
    return api.getCarousels();
  },
  //是否为家庭服务
  changeHome({commit}, home) {
    commit(types.CHANGE_HOME, home);
  },
  getDetailMessage({commit}, id) {
    return api.getDetailMessage(id);
  },
  readMessage({commit}, id) {
    return api.readMessage(id);
  }
}


