
import {CHANGE_ORDERS,CHANGE_UNDONEORDERS,CHANGE_DONEORDERS} from '../types';
import {hgStatus} from '../../utils/util'
console.log(hgStatus);
const state = {
  orders: [],
  undoneOrders: [],
  doneOrders: []
}

const getters = {
  getOrders: state =>  state.orders,
  getundoneOrders : state => state.undoneOrders,
  getdoneOrders : state => state.doneOrders
}

const mutations = {
  [CHANGE_ORDERS](state, playload) {
    playload.orders.items = hgStatus(playload.orders.items)
    state.orders = playload.orders;
  },
  [CHANGE_UNDONEORDERS](state, playload) {
    playload.undoneOrders.items = hgStatus(playload.undoneOrders.items);
    state.undoneOrders = playload.undoneOrders;
  },
  [CHANGE_DONEORDERS](state, playload) {
    playload.doneOrders.items = hgStatus(playload.doneOrders.items);
    state.doneOrders = playload.doneOrders;
  }
}

export default{
  state,
  getters,
  mutations
}


