import {CHANGE_BECARED_DETAIL} from '../types';

const state = {
  beCaredDetail: {}
}

const getters = {
  getBeCaredDetail: state => state.beCaredDetail
}

const mutations = {
  [CHANGE_BECARED_DETAIL](state, beCaredDetail) {
    state.beCaredDetail = beCaredDetail
  }
}

export default {
  getters,
  state,
  mutations
}



